

const Recipe = ({title, image, ingredients}) => {
    return(
        <div className="recipes">
            <img src={image} alt="food" />
            <h2>{title}</h2>
            <ul>
                {ingredients.map(ingredient => (
                    <li key={ingredient.text}>{ingredient.text}</li>
                ))}
            </ul>
        </div>
    )
}

export default Recipe