import './App.css';
import { useEffect, useState } from 'react';
import axios from 'axios';
import Recipe from './Recipe';

function App() {

    const APP_ID = 'c9c742f3';
    const APP_KEY = '584b20b4f847f69dbc5c3fee2e36c930';

    const [recipes, setRecipes] = useState([]);
    const [search, setSearch] = useState('');
    const [query, setQuery] = useState('banana');

    useEffect(() => {
        axios.get(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`)
            .then(res => setRecipes(res.data.hits))
    }, [query])

    return (
        <div className="App">
            <h1>Recipe app</h1>
            <form>
                <input type="text" placeholder="Ingredient..." onChange={e => setSearch(e.target.value)} value={search} />
                <button onClick={e=>{
                    e.preventDefault()
                    setQuery(search)
                    setSearch('')
                }}>Search</button>
            </form>
            <div className="recipe">
                {recipes.map(recipe => (
                    <Recipe
                        key={recipe.recipe.label}
                        title={recipe.recipe.label}
                        image={recipe.recipe.image}
                        ingredients={recipe.recipe.ingredients}
                    />
                ))}
            </div>
        </div>
    );
}

export default App;